.App {
  text-align: center;
  background-color: green;
  background-image: linear-gradient( rgb(1, 99, 1), rgb(18, 49, 85));
  min-height: 95vh;
  width: 100vw;
  padding-top: 25px;
  padding-bottom: 20px;
}
.displaySection{
  min-height: 85vh;
  width: 90vw;
  min-width: 800px;
  background-color: rgba(221, 221, 221, 0.795);
  border-radius: 3px;
  margin-left: 4vw;
  text-align: center;  
  position: relative;
  box-shadow: 0 0 15px rgb(68, 68, 68);
  padding-bottom: 20px;
  padding-left: 15px;
}
.table{
  background-color: rgb(108, 141, 202);  
  width: 48%;
  min-height: 400px;
  padding:10px;
  box-sizing: border-box;
  text-align: center;
  display: inline-block;
  position: relative;
  margin-right: 1%;
  margin-top: 20px;
  box-shadow: 0px 5px 8px rgb(105, 105, 105);
  border: 1px solid black;
  resize: vertical;  
  overflow: hidden;
  vertical-align: top;
}
.title{
  width: 100%;
  background-color: rgba(0,0,0,0);
  outline: none;
  border: none;
  font-size: 25px;  
  text-align: center;
}
.titleSmaller{
  color: rgb(39, 39, 39);
  font-size: 20px;
}
.column{
  height: 320px;
  width: 33%;  
  text-align: center;  
  display: inline-block;
}
.column textarea{
  min-width: 95%;
  max-width: 95%;
  height: 100%;  
  vertical-align: top;
  outline: none;
  border: none;
  display: inline-block;  
  background-color: rgba(224, 224, 224, 0.89);
  
}
.addButton{
  height: 40PX;
  width: 40px;
  border-radius: 50px;
  background-color: green;
  color: white;
  font-size: 40px;
  line-height: 30px;
  text-align: center;
  user-select: none;
  margin: 5px;
  margin-left: -8px;
  position: absolute;
  top: 10px;
  left: 10px;
  z-index: 2;
  cursor: pointer;
}
.addButton:hover{  
  background-color: rgb(106, 184, 106);  
}
.hoverMenuButton{
  height: 25px;
  width: 25px;
  border-radius: 20px;
  background-color: rgba(30, 35, 102, 0.521);
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
}
.hoverMenuButton .hoverMenu{
  display: none;
  height: 100px;
  width: 200px;
  background-color: rgba(0, 81, 202, 0.836);
  border: 1px solid black;
  z-index: 2;
}
.hoverMenuButton:hover .hoverMenu{
  display: inline-block;
  position: absolute;
  top: 0;
  right: 0;  
  border-radius: 5px;
}
.hoverMenuButtonInner{
  width: 100%;
  border-bottom: 1px solid black;
  user-select: none;
}
.hoverMenuButtonInner:hover{
  background-color: rgb(192, 192, 192);
}